/* Размещение кнопки редактирования аккаунта и никнейма */
.accountButtonWrapper {
  position: absolute;
  bottom: 16px;
  left: 16px;
  display: flex;
  align-items: center;
  background-color: rgba(35, 39, 49, 0.637);
  padding: 8px 12px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  cursor: pointer; /* Делаем всю область кликабельной */
}

.accountButtonWrapper:hover {
  background-color: rgba(62, 107, 255, 0.2);
}

/* Кнопка аккаунта */
.accountButton {
  background-color: transparent;
  color: white;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease;
}

.accountButtonWrapper:hover .accountButton {
  transform: scale(1.1);
}

/* Никнейм теперь обрезается при превышении 20 символов */
.username {
  font-family: 'Arial', sans-serif;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  margin-left: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 150px; /* Примерный размер, можно подстроить под дизайн */
}


.accountButtonWrapper:hover .username {
  color: #f0f0f0;
}
